/* Reset Styles */
body, h1, h2, h3, p, ul, ol, li, figure, figcaption, blockquote {
    margin: 0;
    padding: 0;
    
}

body>object {
    width: 100%;
    /* Set width to 100% to match the full width of the page */
    height: 80px;
    /* Set height to 0 to remove vertical space */
}

/* Global Styles */


body {
    margin: 0;
    font-family: 'Playfair Display SC', serif; /* Update font-family */
    font-size: 120%; /* Increase font size by 20% */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    font-size: 120%; /* Increase font size by 20% */
}

h2 {
    font-weight: bold; /* Make headers bold */
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); /* Add text shadow */
}


/* Common Styles */
a {
    text-decoration: none;
}

button {
    cursor: pointer;
}

/* Header Styles */
.logo {
    height: 60px;
    width: auto;
    margin-right: 10px;
}

.main-content {
    display: flex;
    height: calc(100vh - 80px);
    background-image: url('../../public/images/Foods.jpg');
    background-size: cover;
    background-position: center;
    color: #F1F2CC;
}

.main-content h1, h2 {
    text-align: center;
}

.news {
    flex: 3;
    color: #F1F2CC;
    padding: 20px;
    /* Adjust padding as needed */
}

.news-columns {
    flex: 1;
    display: flex;
    justify-content: space-between;
    gap: 40px;
}

.news-column {
    flex: 1;
    /* Updated */
    background-color: white;
    color: #1A3240; /* Text color matching the header background */
    margin: 20px;
}

.news-column ul {
    list-style-type: none;
    padding: 0;
    margin-left: 20px;
    text-align: center;
    img {
        width: 55%;
    }
}

.news-column ul li {
    margin-bottom: 10px;
}

.news-column h3 {
    margin-top: 0;
    margin-bottom: 10px;
}

.news-column.space-between {
    margin-bottom: 60px;
}

.content.A {
    text-align: center;

    img {
        margin: 0 auto; // Center horizontally
        display: block;
    }
}

.category-columns {
    flex: 1;
    display: flex;
    justify-content: space-between;
    gap: 40px;
}

.category-column {
    flex: 1;
    /* Updated */
    background-color: #628370;
    color: #F1F2CC;
    margin: 20px;
}

.category-column ul {
    list-style-type: none;
    padding: 0;
    margin-left: 20px;
}

.category-column ul li {
    margin-bottom: 10px;
}

.category-column h3 {
    margin-top: 0;
    margin-bottom: 10px;
}

.category-column.space-between {
    margin-bottom: 60px;
}

/* Footer Styles */
.footer {
    background-color: #1A3240;
    color: #F1F2CC;
    padding: 20px;
    text-align: center;
    width: 100%;
    position: fixed;
    bottom: 0;
}

.footer a {
    color: #F1F2CC;
    text-decoration: none;
}

#root {
    background-color: #2A4250;
}